import { PrismaAdapter } from "@auth/prisma-adapter";
import type { Role } from "@prisma/client";
import { prisma } from "@/lib/db/prisma";
import nodemailer from "nodemailer";
import type { SendVerificationRequestParams } from "next-auth/providers/email";
import { getServerSession } from "next-auth";
import type { NextAuthOptions } from "next-auth";
import EmailProvider from "next-auth/providers/email";
import GoogleProvider from "next-auth/providers/google";
import VKProvider from "next-auth/providers/vk";
import YandexProvider from "next-auth/providers/yandex";

const authSecret =
  process.env.NEXTAUTH_SECRET ??
  (process.env.NODE_ENV === "development" ? "dev-secret" : undefined);

if (!authSecret) {
  throw new Error("NEXTAUTH_SECRET must be set");
}

const emailFrom = process.env.EMAIL_FROM ?? "no-reply@offerdoc.app";
const emailHost = process.env.EMAIL_SERVER_HOST;
const emailPort = process.env.EMAIL_SERVER_PORT
  ? Number(process.env.EMAIL_SERVER_PORT)
  : undefined;
const emailUser = process.env.EMAIL_SERVER_USER;
const emailPassword = process.env.EMAIL_SERVER_PASSWORD;

const emailConfigured =
  Boolean(emailHost) &&
  Boolean(emailPort) &&
  Boolean(emailUser) &&
  Boolean(emailPassword);

const emailTransport = emailConfigured
  ? nodemailer.createTransport({
      host: emailHost,
      port: emailPort,
      secure: emailPort === 465,
      auth: {
        user: emailUser,
        pass: emailPassword,
      },
    })
  : nodemailer.createTransport({
      streamTransport: true,
      buffer: true,
      newline: "unix",
    });

async function sendVerificationRequest({
  identifier,
  url,
}: SendVerificationRequestParams) {
  await emailTransport.sendMail({
    to: identifier,
    from: emailFrom,
    subject: "   Offerdoc",
    text: `!\n\n  ,  :\n${url}\n\n  15 .`,
    html: `<p>!</p><p>  ,  :</p><p><a href="${url}">  Offerdoc</a></p><p>  15 .</p>`,
  });

  if (!emailConfigured) {
    console.info("[auth]  magic-link ( ):", {
      email: identifier,
      url,
    });
  }
}

type OAuthProviderMeta = {
  id: "google" | "yandex" | "vk";
  name: string;
  isConfigured: () => boolean;
  factory: () => OAuthProvider;
};

type OAuthProvider =
  | ReturnType<typeof GoogleProvider>
  | ReturnType<typeof YandexProvider>
  | ReturnType<typeof VKProvider>;

type WorkspaceAssignableUser = {
  id: string;
  email?: string | null;
  name?: string | null;
  workspaceId?: string | null;
  role?: string | null;
};

function buildWorkspaceName(user: WorkspaceAssignableUser) {
  if (user.name?.trim()) return `${user.name.trim()}   `;
  if (user.email?.trim()) {
    const emailPrefix = user.email.trim().split("@")[0] ?? "workspace";
    return `${emailPrefix}   `;
  }
  return "  ";
}

async function ensureWorkspaceForUser(user: WorkspaceAssignableUser) {
  if (user.workspaceId) return user.workspaceId;

  const workspace = await prisma.workspace.create({
    data: {
      name: buildWorkspaceName(user),
    },
  });

  await prisma.user.update({
    where: { id: user.id },
    data: {
      workspaceId: workspace.id,
      role: "OWNER",
    },
  });

  return workspace.id;
}

const oauthProvidersMeta: OAuthProviderMeta[] = [
  {
    id: "google",
    name: "Google",
    isConfigured: () =>
      Boolean(process.env.GOOGLE_CLIENT_ID && process.env.GOOGLE_CLIENT_SECRET),
    factory: () =>
      GoogleProvider({
        clientId: process.env.GOOGLE_CLIENT_ID!,
        clientSecret: process.env.GOOGLE_CLIENT_SECRET!,
        allowDangerousEmailAccountLinking: true,
      }) as OAuthProvider,
  },
  {
    id: "yandex",
    name: "Yandex ID",
    isConfigured: () =>
      Boolean(process.env.YANDEX_CLIENT_ID && process.env.YANDEX_CLIENT_SECRET),
    factory: () =>
      YandexProvider({
        clientId: process.env.YANDEX_CLIENT_ID!,
        clientSecret: process.env.YANDEX_CLIENT_SECRET!,
      }) as OAuthProvider,
  },
  {
    id: "vk",
    name: "VK ID",
    isConfigured: () =>
      Boolean(process.env.VK_CLIENT_ID && process.env.VK_CLIENT_SECRET),
    factory: () =>
      VKProvider({
        clientId: process.env.VK_CLIENT_ID!,
        clientSecret: process.env.VK_CLIENT_SECRET!,
      }) as OAuthProvider,
  },
];

function optionalOAuthProviders() {
  return oauthProvidersMeta.filter((meta) => meta.isConfigured()).map((meta) => meta.factory());
}

export const enabledOAuthProviders = oauthProvidersMeta
  .filter((meta) => meta.isConfigured())
  .map((meta) => ({ id: meta.id, name: meta.name }));

export const authOptions: NextAuthOptions = {
  adapter: PrismaAdapter(prisma) as NextAuthOptions["adapter"],
  session: {
    strategy: "database",
    maxAge: 60 * 60 * 24 * 30, // 30 
  },
  secret: authSecret,
  pages: {
    signIn: "/auth/signin",
    verifyRequest: "/auth/verify-request",
    error: "/auth/error",
  },
  providers: [
    EmailProvider({
      sendVerificationRequest,
      maxAge: 15 * 60, // 15 
      from: emailFrom,
    }),
    ...optionalOAuthProviders(),
  ],
  callbacks: {
    async session({ session, user }) {
      if (session.user) {
        session.user.id = user.id;
        session.user.role = user.role as Role;
        session.user.workspaceId = user.workspaceId;
      }
      return session;
    },
  },
  events: {
    async createUser({ user }) {
      await ensureWorkspaceForUser(user as WorkspaceAssignableUser);
    },
    async signIn({ user }) {
      await ensureWorkspaceForUser(user as WorkspaceAssignableUser);
    },
  },
  debug: process.env.NODE_ENV === "development" && process.env.NEXTAUTH_DEBUG === "true",
};

export function getServerAuthSession() {
  return getServerSession(authOptions);
}
